

.App-logo {
  height: 10vmin;
  pointer-events: none;
}

.App-logoMobile {
  height: 8vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }

  .App-logoMobile {
    animation: App-logo-spin infinite 20s linear;
  }

}

